import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Dashboard from './../../common/dashboard';
import { MDBDataTable } from 'mdbreact';
import { getSizes, deleteSize } from './../../../services/mediaHouseService';
import { toast } from 'react-toastify';

class Size extends Component {
    state = { 
        data: {
            columns : [
                {label: 'Name',field: 'name',sort: 'asc', width: 300},
                {label: 'Edit',field: 'edit',sort: false, width: 50},
                {label: 'Delete',field: 'remove',sort: false, width:50}],
            rows: []
        }
    };

    async componentDidMount() {
        try{
            const {data} = await getSizes();
            let sizes = [];
            sizes = data.result.data.map((type, index) => {
               return {
                    name: type.size,
                    edit: <Link to={`/sizes/edit/${type.size}`} className="btn btn-primary btn-sm"><i className="fas fa-edit"></i></Link>,
                    remove: <button value={type.size} data-index={index} onClick={this.removeSize} className="btn btn-danger btn-sm"><i className="fas fa-trash"></i></button>
                };
            })
            const stateObj = {...this.state.data};
            stateObj['rows'] = sizes;
            this.setState({ data: stateObj });
        } catch (e) {
            return null;
        }
    }

    removeSize = async (ev) => {
        let response = window.confirm("Are you sure you want to remove "+ev.currentTarget.value+"?");
        if(response) {
            const val = ev.currentTarget.value;
            const index =  ev.currentTarget.dataset.index;
            const data = await deleteSize(val);
            if(data.status === 200){
                const stateObj = {...this.state.data};
                delete stateObj.rows[index];
                this.setState({ data: stateObj });
                toast.success('Size deleted');
            }
        }
    }

    render() { 
        return ( 
            <Dashboard>
                <div className="white-bg">
                            <h2 className="section-title">Manage sizes</h2>
                            <Link to="/sizes/create" className="btn abtn abtn-2" style={{padding: '10px' }}>Create Size Metric</Link>
                            <div className="table-responsive">
                            <MDBDataTable  striped bordered hover
                                       data={this.state.data}
                                       paging={false} />
                            </div>
                        </div>
            </Dashboard>
         );
    }
}
 
export default Size;